$primary: #56a43c;

@use "@material/theme" with (
  $primary: $primary,
  $secondary: #feeae6,
  $background: #FFF,
  $error: #B00020
);
@use "@material/theme/mdc-theme";

@use "@material/typography/mdc-typography";
@use "@material/card";
@use "@material/fab";
@use "@material/floating-label/mdc-floating-label";
@use "@material/line-ripple/mdc-line-ripple";
@use "@material/notched-outline/mdc-notched-outline";
@use "@material/textfield";

@include card.core-styles;
@include fab.core-styles;
@include textfield.core-styles;

/** Start new Styles **/

@import "font";
@import "hero";
@import "textfield";
@import "section";

/** End new Styles **/

html, body {
  margin: 0;
  padding: 0;
}

.mdc-text-field__input, .mdc-floating-label  {
  font-family: "Minecraft-Seven", "Roboto Flex", Roboto, "Segoe UI", sans-serif;
}

.mdc-button__label {
  font-family: "Minecraft-Five-Bold", "Roboto Flex", Roboto, "Segoe UI", sans-serif;
}